<template>
	<view v-show="article.content.length>0" class="f-html">
		<view class="f-content">
			<u-parse :html="article.content" :tag-style="article.style" :selectable="true"></u-parse>
		</view>
		<view v-show="commentList.length>0" class="f-comment">
			<view class="comment-title">已有<view style="color: #ea6060; padding: 0 10upx;">{{freebuf.comment_count}}</view>条评论</view>
			<view class="comment-content" v-for="(item,index) in commentList" :key="index">
				<fb-comment :comment="item" :author="freebuf.username"></fb-comment>
			</view>
		</view>
		<view class="back-to-top" :style="{'display':(showBackToTop===true? 'block':'none')}">
		  <uni-icons class="icons" type="rocket" size="36" color="#2db362" @click="backToTop"></uni-icons>
			<!-- <img> -->
		</view>
	</view>
</template>

<script>
	// import u-parse from "@/components/uView/u-parse/u-parse.vue"
	import fbComment from "@/components/freebuf/fb-comment/fb-comment.vue"
	export default {
		data() {
			return {
				article: {
					content: ``,
					style: {
						h1: "max-width: 100%; line-height: 22px; font-size: 18px; font-family: PingFangSC-Medium, PingFang SC; font-weight: 600; color: rgba(0, 0, 0, 0.85); padding-left: 15px; margin: 25px 0; position: relative;",
						h2: "font-size: 18px; font-weight: bold; color: #373838; margin: 30px 0px 15px 0px;", //border-left: solid 5px #1ea65c;
						h3: "font-size: 16px; font-weight: bold; color: #0070c0; margin: 20px 0px 15px 0px;",
						//a: "color: #19a358; overflow: hidden; text-overflow: ellipsis; display: -webkit-box;  -webkit-line-clamp: 1; -webkit-box-orient: vertical;text-decoration: none;",
						a: "color: #19a358;text-decoration: none;",
						p: "font-size: 15px; line-height: 26px; word-wrap: break-word; word-break: break-word; margin-top: 10px;color: #585858;",
						pre: "display: block; padding: 9.5px; margin: 0 0 15px; font-size: 13px; line-height: 1.42857143; color: #333; word-break: break-all; word-wrap: break-word; background-color: #F3F3F3; border: 1px solid #E4E4E4; border-radius: 4px;font-family: Menlo, Monaco, Consolas, 'Courier New', monospace;",
						code: "font-size: 90%; word-break: break-word;font-family: inherit;padding: 0 5px;"


					},
				},
				freebuf: {},
				commentList: [],
				link: '',
				showBackToTop:false
			}
		},
		components: {
			fbComment
		},
		onLoad(receive) {
			if (receive) {
				console.log(receive)
				this.freebuf = receive
				// this.getComment()
				this.parseContent()
			}
		},
		methods: {
			async getComment() {
				var res = await this.$surf.getComment()
				this.commentList = res.data[0].comment
				console.log(this.commentList)
			},
			async parseContent() {
				console.log("12")
				uni.showLoading({
					title: "冲浪中...",
					mask: true
				});
				uni.request({
					url: this.freebuf.link,
					method: 'GET',
					success: res => {
						var feedBack = res.data
						var title = feedBack.match(/<title>\s*.*?\s*<\/title>/)
						var a_c = feedBack.match(/<div\s*class="articlecontent"\s*\S*?>([\s\S]*?)<\/div>-->\s*<\/div>/)
						if (!a_c) {
							//提示
							return
						}
						a_c = a_c[0].replace(/<div id="toc_container".*<\/div>/g, '')
						a_c = a_c.replace(/src="https:\/\/www.freebuf.com\/buf\/themes\/freebuf\/images\/grey.gif"/g, '')
						a_c = a_c.replace(/data-original/g, 'src')
						a_c = a_c.replace(/alt\s*=\s*"\s*"\s*width="([\s\S]*?)"\s*height="([\s\S]*?)"\s*?/g, '')
						a_c = a_c.replace(/<noscript>.*<\/noscript>/g, '')
						a_c = a_c.replace(/<style>([\s\S]*?)<\/style>/g, '')
						a_c = a_c.replace(/<!--(.|[\r\n])*?-->/g, '')
						// a_c = a_c.replace(/div\s*style\s*=\s*"position\s*:\s*relative;/g, 'div style="position: relative;padding-right:160px')
						a_c = a_c.replace(/href="\.\./g, 'href="https://www.freebuf.com/mob')
						a_c = a_c.replace(/<script\b[^<]*(?:(?!<\/script>)<[^<]*)*<\/script>/g, '')
						a_c = a_c.replace(/<div class="wechatcodeDiv">([\s\S]*?)<\/div>\s*<div class="newlabelDiv">([\s\S]*)<div class="share-new clearfix">\s*<\/div>\s*<\/div>/g,'')
						a_c = a_c.replace(/padding-right: 160px;/,'padding-right: 160px; float: left; font-size: 25px; line-height: 34px; margin: 0px; padding-bottom: 5px; font-weight: bold; max-height: 150px; overflow: hidden;')
						a_c = a_c.replace(/<span class="name"><a/,'<span class="name"><a style="display: inline-block; margin-right: 4px; color: #19a358 !important;text-decoration: none !impotant;"')
						a_c = a_c.replace(/<span class="name"/,'<span class="name" style="float: left; height: 22px; padding-left: 18px; padding-right: 0px; height: 20px; line-height: 16px; background: url(https://image.3001.net/images/new/news-icon.png) no-repeat left 3px;"')
						a_c = a_c.replace(/<span class="time"/,'<span class="time" style="float: left; height: 22px; padding-left: 17px; padding-right: 12px; color: #b8b8b8; background: url(https://image.3001.net/images/new/news-icon.png) no-repeat left -27px;"')
						a_c = a_c.replace(/<span class="look"/,'<span class="look" style="float: left; height: 22px; padding-right: 13px; color: #666;"')
						// a_c = a_c.replace(/<span class="tags>\s*<a/g, '<span class="tags><a style="display: inline-block; height: 23px; line-height: 21px; color: #666666; padding: 0 10px; border: 1px solid #e2e2e2; -moz-border-radius: 3px; -webkit-border-radius: 3px; border-radius: 3px; margin-right: 6px; font-family: "微软雅黑"; text-decoration: none;"')
						a_c = a_c.replace(/<span class="tags"/, '<span class="tags" style="display: none;"')
						a_c = a_c.replace(/<span class="tags_01"/, '<span class="tags_01" style="display: none;"')
						a_c = a_c.replace(/<span class="icon-f"/,'<span class="icon-f" style="float: left; height: 22px; padding-right: 11px; margin: 5px 0 0; height: 12px; overflow: hidden;"')
						a_c = a_c.replace(/<div><a/g, '<div><a style="color: #666666 !important; text-decoration: none !important;"')
						a_c = a_c.replace(/"creatByself"/,'"creatByself" style="bottom: -150px !important; width: 40% !important; right: -20px !important;"')
						a_c = a_c.replace(/"property"/,'"property" style="float: left; width: 100%; line-height: 21px; margin-top: 5px; margin-bottom: 60px; padding-top: 10px; border-top: solid 1px #f0f0f0;"')
						a_c = a_c.replace(/right:16px;bottom/, 'right:0px;bottom')
						a_c = a_c.replace(/z-index: 500;/, 'z-index: 500;bottom:-80px;right:0px !important;width: 45%; !important')
						this.article.content = title + a_c
						this.parseComment()
					},
					fail: (e) => {
						//整个弹窗提示
					},
					complete() {
						uni.hideLoading()
					}
				});
			},
			async parseComment() {
				uni.request({
					url: "https://www.freebuf.com/fapi/frontend/post/comments?id=#&order=2&page=1".replace('#', this.freebuf.id),
					method: 'GET',
					success: res => {
						var comm_1 = res.data.data.data_list
						// console.log(feedBack)
						uni.request({
							url: "https://www.freebuf.com/fapi/frontend/post/comments?id=#&order=2&page=2".replace('#', this.freebuf.id),
							method: 'GET',
							success: res => {
								var comm_2 = res.data.data.data_list
								this.commentList.push(...comm_1, ...comm_2)
							},
							fail: (e) => {
								//整个弹窗提示
							},
							complete() {}
						});
					},
					fail: (e) => {
						//整个弹窗提示
					},
					complete() {}
				});
			},
		    backToTop() {
		        uni.pageScrollTo({
		            scrollTop: 0,
		            duration: 300
		        });
		    },
		    onPageScroll(e){ 
		        if(e.scrollTop>600){
		            this.showBackToTop=true
		        }else{
		            this.showBackToTop=false
		        }
		    }
		}
	}
</script>

<style lang="scss" scoped>
	.f-html {
		max-width: 100%;
		font-size: 24upx;
		padding: 20upx;
		font-family: "微软雅黑";
		background: url(https://image.3001.net/images/new/bg.jpg) repeat-x;

		.f-content {
			box-sizing: border-box;
			border-radius: 4px;
			border: 1px solid transparent;
			box-shadow: 0 1px 1px rgba(0, 0, 0, .05);
			padding: 10px 15px;
			background-color: #fff;
		}

		.f-comment {
			margin-top: 30px;

			.comment-title {
				font-size: 20px;
				font-weight: bold;
				margin: 0px;
				height: 40px;
				border-bottom: solid 2px #1ea65c;
				display: flex;
			}

			.comment-content {
				padding: 0 15px;
				border-top: 1px solid #eee;
			}
		}
		
		.back-to-top {
				position: fixed;
				width: 100upx;
				height: 100upx !important;
				right: 20upx;
				bottom: 200upx;
				// color: #2db362;
				border-radius: 50%;
				background-color: #fff;
				box-shadow: 4upx 4upx 12upx 0 hsla(0,0%,85.1%,.62);
				text-align: center;
				.icons {
					width: 60upx;
					margin-top: 8upx;
					display: flex;
					padding-left: 18upx;
					// content:url("https://p0.ssl.qhimg.com/t0179ac3294ef926b8c.png");
					
				}
		}
	}
</style>
